import { TICK_TIME } from "./actions";
import { defineState } from "redux-localstore";
import { getState } from "redux-localstore";

const defaultState = getState() || {
  cities: null
};

const initialState = defineState(defaultState)("time_reducer");

export function time_reducer(state = initialState, action) {
  switch (action.type) {
    case TICK_TIME:
      let curTime = state.time || { day: 1, month: 1, year: 1 };
      let newDay = curTime.day + 1;
      let newMonth = curTime.month;
      if (newDay == 31) {
        newDay = 1;
        newMonth = newMonth + 1;
      }
      let newYear = curTime.year;
      if (newMonth == 13) {
        newMonth = 1;
        newYear = newYear + 1;
      }
      let time = { day: newDay, month: newMonth, year: newYear };
      return {
        ...state,
        time: time
      };
    default:
      return state;
  }
}



