import React from "react";
import { connect } from "react-redux";
import AddCity from "./AddCity";
import { addCity } from "./actions";
import { withRouter } from "react-router-dom";
import { seedRandomPopulation } from "../../modules/Population/utils";

const mapDispatchToProps = dispatch => ({
  dispatchAddCity: cityName => {
    let city = { name: cityName, pop: seedRandomPopulation() };
    console.log("====city====");
    console.log(city);
    dispatch(addCity(city));
  }
});

const mapStateToProps = state => {
  return {
    cities: state.cities
  };
};

const AddCityContainer = connect(mapStateToProps, mapDispatchToProps)(AddCity);

export default AddCityContainer;



